import React, {Component} from 'react'
import {BrowserRouter as Router, Route, Link, Switch} from "react-router-dom"
import './css/tailwind.css'
import MainMenu from './components/MainMenu'
import Film from './components/Film'
import Character from './components/Character'
import Location from './components/Location'
import Error from './components/Error'

import characters from './data/characters.json'
import films from './data/films.json'
import locations from './data/locations.json'
import species from './data/species.json'

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: {
        films: films,
        characters: characters,
        locations: locations,
        species: species
      }
    }
  }

  render() {
    return (<Router>
      <div>
        <header className="bg-grey-light w-full h-16 flex justify-around">
          <div className=" relative bg-grey-light w-1/5 flex justify-center">
            <Link to="/" className="ml-12  flex items-center justify-end text-3xl  no-underline text-black">Studio Ghibli
            </Link>
          </div>
          <MainMenu/>
        </header >

        <Switch>
          <Route exact="exact" path='/' render={(props) => <Film {...props} data={this.state.data}/>}/>

          <Route path='/films/:id' render={(props) => <Film {...props} data={this.state.data}/>}/>

          <Route path='/characters/:id' render={(props) => <Character {...props} data={this.state.data}/>}/>

          <Route path='/locations/:id' render={(props) => <Location {...props} data={this.state.data}/>}/>

          <Route component={Error}/>

        </Switch>

        <footer className="bg-grey w-full h-16 flex justify-around">
          <div className="py-4">
            Data supplied by &nbsp;
            <a href="https://github.com/janaipakos/ghibliapi" className="text-white">
              Studio Ghibli API
            </a>
          </div>
        </footer>
      </div>

    </Router>)
  }
}

export default App;
