import React from 'react'
import {Link} from "react-router-dom"
import _ from 'lodash'

class SideMenu extends React.Component {
  render() {
    const data = this.props.data
    const page = this.props.page

// check data type coming in
    let listItem
    if (data[0].name) {
      listItem = 'name'
    } else if (data[0].title) {
      listItem = 'title'
    }

    return (<div className="bg-grey-lightest pt-2 w-1/4  ">
      <h2 className="pl-4 pb-2 font-normal">{_.upperFirst(this.props.page)}</h2>

      <ul className="list-reset mt-2 mb-4 mx-4">
        {
          data.map((item) =>
          <li className="m-1" key={item.id}>
            <Link to={"/"+ page +"/" + item.id} className="text-red no-underline ">{item[listItem]}</Link>
          </li>)
        }
      </ul>

    </div>)
  }
}

export default SideMenu
