import React from 'react'
import SideMenu from './SideMenu'
import _ from 'lodash'

class Film extends React.Component {
  render() {
    const data = this.props.data
    const { params } = this.props.match
    const filmId = params.id ? params.id :'2baf70d1-42bb-4437-b551-e5fed5a87abe'

    const filmsInfo = _.find(data.films, function(o) {return o.id === filmId})

    return (
      <div className="bg-grey-lightest w-full flex justify-center pt-5">
      <SideMenu data={data.films} page={'films'}/>
      <div className="bg-grey-light w-3/5">
    <h2  className="pl-4 pt-2 pb-2 font-normal">{filmsInfo.title}</h2>
    <div className="my-2  mx-4">
      <p className="my-1 pb-3">{filmsInfo.description} </p>
      <p className="my-1">Director: {filmsInfo.director} </p>
      <p className="my-1">Producer: {filmsInfo.producer} </p>
      <p className="my-1">Year:  {filmsInfo.release_date} </p>
      <p className="my-1">Rotten Tomatoes Scorege:  {filmsInfo.rt_score} </p>
    </div>
      </div>
      </div>
    )

  }
}

export default Film
