import React from 'react'
import SideMenu from './SideMenu'
import _ from 'lodash'
import {Link} from "react-router-dom"

class Character extends React.Component {
  render() {
    const data = this.props.data
    const {params} = this.props.match

    const charInfo = _.find(data.characters, function(o) {
      return o.id === params.id
    })

    // extract film links from json
    let film
    let filmList = []
    let i
    for (i = 0; i < charInfo.films.length; i++) {
      film = charInfo.films[i]
      film = film.toString()
      film = film.slice(38)
      film = _.find(data.films, function(o) {
        return o.id === film
      })
      film = <li key={film.id} className="m-1">
        <Link to={"/films/" + film.id} className="text-red no-underline">{film.title}</Link>
      </li>
      filmList.push(film)
    }
    

    return (<div className="bg-grey-lightest w-full flex justify-center pt-5">
      <SideMenu data={data.characters} page={'characters'}/>
      <div className="bg-grey-light w-3/5">
        <h2 className="pl-4 pt-2  pb-2 font-normal">{charInfo.name}</h2>
        <div className="my-2  mx-4">
          <p className="my-1">Gender: {charInfo.gender}
          </p>
          <p className="my-1">Hair Color: {charInfo.hair_color}
          </p>
          <p className="my-1">Eye Color: {charInfo.eye_color}
          </p>
          <p className="my-1">Age: {charInfo.age}
          </p>
        </div>
        <h2 className="pl-4 mt-4 mb-2 font-normal">Films</h2>
        <ul className="list-reset mx-4">{filmList}</ul>
      </div>
    </div>)

  }
}

export default Character
