import React from 'react'
import SideMenu from './SideMenu'
import _ from 'lodash'
import {Link} from "react-router-dom"


class Location extends React.Component {
  render() {
    const data = this.props.data
    const {params} = this.props.match

    const locationsInfo = _.find(data.locations, function(o) {
      return o.id === params.id
    })


    // extract film links from json
    let film
    let filmList = []
    let i
    for (i = 0; i < locationsInfo.films.length; i++) {
      film = locationsInfo.films[i]
      film = film.toString()
      film = film.slice(38)
      film = _.find(data.films, function(o) {
        return o.id === film
      })
      film = <li key={film.id} className="m-1">
        <Link to={"/films/" + film.id} className="text-red no-underline">{film.title}</Link>
      </li>
      filmList.push(film)
    }

    // extract character links from json
    let character
    let charList = []
    let j
    for (j = 0; j < locationsInfo.residents.length; j++) {
      character = locationsInfo.residents[j]
      character = character.toString()
      character = character.slice(39)
      character = _.find(data.characters, function(o) {
        return o.id === character
      })
      character = <li key={character.id} className="m-1">
        <Link to={"/characters/" + character.id} className="text-red no-underline">{character.name}</Link>
      </li>
      charList.push(character)
    }



    return (<div className="bg-grey-lightest w-full flex justify-center pt-5">
      <SideMenu data={data.locations} page={'locations'}/>
      <div className="bg-grey-light w-3/5">
        <h2 className="pl-4 pt-2  pb-2 font-normal">{locationsInfo.name}</h2>
        <div className="my-2  mx-4">
          <p className="my-2">Climate: {locationsInfo.climate}
          </p>
          <p className="my-2">Terrain: {locationsInfo.terrain}
          </p>
          <p className="my-2">Surface Water: {locationsInfo.surface_water}
          </p>
        </div>
        <h2 className="pl-4 mt-4 mb-2 font-normal">{charList.length >= 1 ? 'Characters That Visit' : ""}</h2>
        <ul className="list-reset mx-4">{charList}</ul>
        <h2 className="pl-4 mt-4 mb-2 font-normal">Films</h2>
        <ul className="list-reset mx-4">{filmList}</ul>
      </div>
    </div>)
  }
}

export default Location
